import React from "react";

const Event = ({ currentLanguage }) => {
  return (
    <section className="event">
      {currentLanguage === "hu" ? (
        <div className="container">
          <br></br>
          {/* <h2>Honlapunkat 2024. februr kzepn frisstjk a buszok regisztrcijval s a tovbbi programokkal.</h2> */}
          <h1>RENDEZVNY</h1>
          <p>
            2024. mjus 11-n negyedik alkalommal kerl megrendezsre a
            Nemzetkzi Ikarus tallkoz Tapolcn. A rendezvny 2016-ban indult
            tjra, azzal a cllal, hogy egyrszt a nyugati orszgrszben is
            legyen egy, a magyar buszgyrts termkeit bemutat rendezvny,
            msrszrl a pratlan vekben Nmetorszgban megrendezett Ikarus
            tallkozk pros vekben itthon megtartott testvr rendezvnyv
            vljon.
          </p>
        </div>
      ) : (
        <div className="container">
          <br></br>
          {/* <h2>We will update our website in mid-February 2024 with bus registration and additional programs.</h2> */}
          <h1>THE EVENT</h1>
          <p>
            On the 11<sup>th</sup> of may 2024. the International Ikarus Meeting will be
            held at Tapolca for the fourth time. The first meeting in 2016. was
            organized for two purposes. One was to have a gathering of hungarian
            made buses in the western part of Hungary, and the other was to have
            a sibling event in the even years for the german Ikarus meeting held
            in every odd year.
          </p>
        </div>
      )}
    </section>
  );
};

export default Event;
