import React from "react";

const Prices = ({ currentLanguage }) => {
  return (
    <section className="event" id="prices">
      {currentLanguage === "hu" ? (
        <div className="container">
          <h1>RAK</h1>
          <table>
            <tbody>
              <tr>
                <td>
                  <strong>Belps a rendezvnyre</strong>
                </td>
                <td>INGYENES</td>
              </tr>
              <tr>
                <td>
                  <strong>Vrosi nosztalgia krjratok</strong>
                </td>
                <td>1500 Ft/f (6 ves korig ingyenes)</td>
              </tr>
              <tr>
                <td>
                  <strong>Ikarus 481-es busszal a Dob laktanyhoz, tankbrls.hu gyjtemnynek megtekintse</strong>
                </td>
                <td>3500 Ft/f</td>
              </tr>
              <tr>
                <td>
                  <strong>Kli-medencei busz felvonuls</strong>
                </td>
              </tr>
              <tr>
                <td>- Ikarus 30, 311, 55, 66 tpus buszokkal</td>
                <td>3000 Ft/f</td>
              </tr>
              <tr>
                <td>- Egyb Ikarus tpus buszokkal</td>
                <td>2000 Ft/f</td>
              </tr>
              {/* <tr>
                <td colSpan="2">
                  <small>
                    (az autbuszokon becsletkassza zemel, amellyel a jrmvek
                    tulajdonosait tmogathatjk, hogy megjelentek a
                    rendezvnyen)
                  </small>
                </td>
              </tr> */}
              <tr>
                <td>
                  <strong>Tombola sorsjegy</strong>        
                </td>
                <td>500 Ft/db</td>
              </tr>
              <tr>
                <td colSpan="2">
                  <small>
                    A Tombola fnyeremnye: 2 jszaka 2 f rszre reggelivel a Hotel
                    Halszkertben, Badacsonyban
                  </small>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      ) : (
        <div className="container">
          <h1>PRICES</h1>
          <table>
            <tbody>
              <tr>
                <td>
                  <strong>Admission</strong>
                </td>
                <td>FREE</td>
              </tr>
              <tr>
                <td>
                  <strong>Nostalgic city-bus services</strong>
                </td>
                <td>1500 HUF/person/round (free up to the age of 6)</td>
              </tr>
              <tr>
                <td>
                  <strong>Take the Ikarus 481 bus to the Dob barracks, view the tankbrls.hu collection</strong>
                </td>
                <td>3500 HUF/person</td>
              </tr>
              <tr>
                <td>
                  <strong>Old timer bus parade in the Kli-basin</strong>
                </td>
                {/* <td>FREE</td> */}
              </tr>
              <tr>
                <td>- with Ikarus 30, 311, 55, 66 bus types</td>
                <td>3000 HUF/person</td>
              </tr>
              <tr>
                <td>- with other Ikarus bus types</td>
                <td>2000 HUF/person</td>
              </tr>
              <tr>
                <td colSpan="2">
                  {/* <small>
                    (On the attending buses there will be an honesty jar.
                    Money gathered in this jar supports the owner of the
                    vehicle as thanks for displaying it on the event.)
                  </small> */}
                </td>
              </tr>
              <tr>
                <td>
                  <strong>Raffle 'Tombola' ticket</strong>
                </td>
                <td>500 HUF/piece</td>
              </tr>
              <tr>
                <td colSpan="2">
                  <small>
                    The main prize of the Raffle: 2 nights for 2 people with breakfast at the
                    Hotel Halszkert in Badacsony
                  </small>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      )}
    </section>
  );
};

export default Prices;
